@import '_colors';
@import '_layout';
@import '_constants';

.overview-chart-container {
  // border: 1px solid blue;
  display: flex;
  height: 100%;

  .left-side-panel {
    // height: 96%;
    width: 60%;
    display: flex;
    flex-flow: column;

    .spanchart-controls-container-left {
      display: flex;
      flex-flow: row;
      justify-content: space-between;
      height: 80px;
      margin: 5px 20px 10px 0px;
      text-align: center;
      flex: 0 0 auto;

      .overview-title-container {
        // flex-direction: column;
        // justify-content: center;
        text-align: left;

        .overview-title {
          color: $titleBlue;
          font-size: 1.5rem;
          font-weight: bold;
        }

        .intro-text {
          margin-top: 10px;
        }
      }


      .span-sort-control-container {
        width: 30%;
        display: inline-block;
        height: 100%;
      }

    }

  }

  .right-side-panel {
    // border: 1px solid orange;
    height: 100%;
    width: 40%;
    display: flex;
    flex-flow: column;
    align-items: stretch;
    justify-content: flex-start;
    overflow: hidden;

    .spanchart-controls-container-right {
      // border: 1px solid blue;
      flex: 0 1 auto;
      margin: 0px 0px 20px 0px;

      .main-index-controls-label-container {
        margin-bottom: 6px;

        .main-index-controls-label-span {
          color: $interfaceGray;
          font-weight: bold;
          font-size: 0.7rem;
          text-transform: uppercase;
        }
      }


      .main-index-controls-container {
        width: 100%;
        height: 50px;
        display: flex;
        flex-flow: row;
      }

    }

    .radar-side-bar {}

    .radar-sidebar-container {
      height: 100%;
      display: flex;
    }
  }

}


.span-chart-container {
  // border: 1px solid red;
  // overflow: visible;
  flex: 1 1 auto;
}


@media only screen and (max-width: $mobileBreakpoint) {

  .overview-chart-container {

    .overview-title-container {
      display: none !important;
    }

    .span-sort-control-container {
      width: 100% !important;
    }

    .right-side-panel {
      display: none !important;
    }

    .left-side-panel {
      width: 100% !important;
    }
  }
}