@import '_colors';

$text-large: 20px;
$text-medium: 16px;
$text-small: 14px;

$gray-text: #646464;

.subtext-block {
    font-size: $text-small;
    color: $gray-text;
    margin-top: 0px;
    line-height: 22px;
}

.field-label {
    color: $interfaceGray;
    text-transform: capitalize;
    font-size: 12px;
    line-height: 14px;
}

.compare-selector-container .field-label {
    margin-bottom: 6px;
}

h2 {
    font-size: $text-large;
    font-weight: bold;
    margin-top: 0px;
    margin-bottom: 8px;
}

h3 {
    font-size: $text-medium;
    margin-top: 0px;
    margin-bottom: 10px;
}