@import '_colors';
@import '_constants';

.country-view-container {
  height: 100%;
  display: flex;
  margin-right: 40px;
  margin-left: 40px;
  margin: auto;
  max-width: 1250px;
  flex-flow: column;

  .country-summary {
    font-weight: bold;
    color: $interfaceGray;
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 40px;
  }

  .country-view-top-container {
    // display: flex;
    justify-content: space-between;
    height: 100%;
    position: relative;
    // height: 400px;


    .country-title-and-description-container {
      width: 60%;

      .country-name {
        color: $titleBlue;
        font-size: 2rem;
      }
    }

    .country-selector-container {
      width: 350px;
      position: absolute;
      top: 0;
      right: 0;

    }

  }

  .country-view-radar-container {
    height: fit-content;
    margin-bottom: 150px;
    // margin-top: 350px;

    .radar-grid {
      display: flex;
      flex-flow: column;

      .radar-grid-row {
        display: flex;
        flex-flow: row;

        .radar-grid-box {
          width: 50%;
          height: 500px;
        }
      }
    }
  }

  .country-line-and-table-container {
    margin-bottom: 30px;
    display: flex;
    flex-flow: row;

    .line-chart-container {
      margin-bottom: 80px;
      width: 66%;
    }

    .indices-table {
      width: 34%;
    }

  }

}


@media only screen and (max-width: $mobileBreakpoint) {

  .country-view-container {
    width: 100% !important;

    .country-view-top-container {
      flex-flow: column !important;
      flex-direction: column-reverse !important;

      .country-selector-container {
        width: 100% !important;
      }

      .country-title-and-description-container {
        width: 100% !important;
      }
    }
  }

  .radar-grid-row {
    flex-flow: column !important;

    .radar-grid-box {
      width: 100% !important;
    }
  }


}