@import '_colors';
@import '_layout.scss';
@import '_constants';

.compare-chart-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  flex: 1 1 auto;


  .compare-selectors-and-title-container {
    flex: 0 1 auto;
    display: flex;
    justify-content: space-between;
    align-items: left;
    ;

    .compare-chart-title {
      display: flex;
      flex-flow: column;
      justify-content: flex-start;
      color: $titleBlue;
      font-weight: bold;
      font-size: 1.5rem;
    }

    .intro-text {
      margin-top: 10px;
    }

    .compare-selectors-container {

      display: flex;
      justify-content: center;
      align-items: center;


      .swap-button {
        display: inline-block;
        width: 48px;
        height: 48px;
        border: 1px solid $interfaceGray;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        margin-right: 10px;
        border-color: hsl(0, 0%, 80%);

        &:hover {
          background-color: rgba(111, 0, 255, .30);
        }
      }
    }
  }

  .right-side-container {
    display: flex;
    flex-flow: column;
    justify-content: stretch;
    width: 40%;

    .under-over-chart-container {}

    .top-text-panel {
      // border: 1px solid orange;
      width: 100%;

      .text-area {
        display: inline-block;
        width: 90%;
      }

      .info-button-container {
        display: inline-block;
        width: 9%;
        height: 60px;
        float: right;

        .info-button {
          border: 1px solid $interfacePurpleColor;
          border-radius: 4px;
          color: $interfacePurpleColor;
          float: right;
          height: 30px;
          width: 30px;
          line-height: 30px;
          cursor: pointer;

          .info-button-icon {
            display: block;
            margin: auto;
            height: 30px
          }

          &:hover {
            background-color: rgba(101, 33, 245, 0.4);
            border: none;
          }
        }
      }
    }


    // .info-button {
    //   width: 40px;
    //   height: 40px;
    //   border: 1px solid red;
    //   position: relative;
    //   left: 100%;
    //   transform: translate(-40px, 0px);
    // }

    .subtext-block {
      // border: 1px solid orange;
      // width: 90%;
    }
  }

  .compare-plots-container {

    flex: 1 1 auto;
    -webkit-flex: 1 1 100%;
    display: flex;



  }

}

@media only screen and (max-width: $mobileBreakpoint) {


  .compare-subtitle-container {
    display: none !important;
  }

  .compare-selectors-and-title-container {
    margin-left: 70px !important;
  }

  .compare-chart-title {
    display: none !important;
  }

  .compare-selectors-container {
    flex-flow: column !important;

    .swap-button {
      display: none !important;
    }
  }



  .compare-plots-container {

    .right-side-container {
      display: none !important;
    }

    .scatter-plot-container {
      width: 100% !important;
    }
  }
}