@import '_constants';

body>#root>div {
  height: 100vh;

}

.App {

  display: flex;
  flex-flow: column;
  max-width: 2200px;
  margin: 0 auto;
}

.nav-bar-container {
  flex: 0 0 auto;
}

.app-content-container {
  position: relative;
  flex: 1 1 auto;
  margin: 30px;
  margin-top: 15px;
  position: relative;
  height: 100%;
  display: flex;
  ;
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

@media only screen and (max-width: $mobileBreakpoint) {

  .nav-bar-container {
    // display: none !important;
  }

}