@import '_colors';

.main-index-selector {
  border: 1px solid #DBDBDB;
  border-radius: 4px;
  margin: 0px 10px 0px 0px;
  flex: 1 1;
  cursor: pointer;
  padding: 5px;


  display: flex;
  flex-direction: column;
  justify-content: center;
  // align-content: center;
  // text-align: center;
  // font-size: 0.8rem;
}

.selector-last {
  margin-right: 0px;
}

.index-is-selected {
  border: 1px solid purple;
  background-color: plum;
}

.index-is-selected-mean_overall {
  border: 1px solid $meanOverallSolid;
  background-color: $meanOverallLighter;
}

.index-is-selected-mean_inputs {
  border: 1px solid $meanInputsSolid;
  background-color: $meanInputsLighter;
}

.index-is-selected-mean_throughputs {
  border: 1px solid $meanThroughputsSolid;
  background-color: $meanThroughputsLighter;
}

.index-is-selected-mean_outputs {
  border: 1px solid $meanOutputsSolid;
  background-color: $meanOutputsLighter;
}


.main-index-tag-container {
  line-height: 100%;

  .main-index-color-indicator {
    border-radius: 5px;
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-left: 5px;
  }

  .main-index-name-label {
    display: inline-block;
    font-size: 15px;
    line-height: 15px;
    font-weight: bold;
    margin-left: 10px;
  }
}