@import 'colors';

a {
  color: $interfacePurpleColor;
  text-decoration: none;
}

.country-tag {
  border: 1px solid #DCDCDC;
  border-radius: 5px;
  flex: 1 1;
  margin-right: 5px;


  .tag-top-bar {
    border-bottom: 1px solid #DCDCDC;
    position: relative;
    height: 50px;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;

    .country-tag-name {
      padding-left: 5px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      font-size: 15px;
      font-weight: bold;


    }

    .tag-top-bar-controls-container {
      font-size: 0.7rem;
      display: flex;
      flex-flow: row;

      .tag-details-button {
        margin: 0px 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: $interfacePurpleColor;
        font-weight: bold;
        font-size: 14px;
        cursor: pointer;
        border-radius: 5px;

      }

      .tag-clear-button {
        margin: 0px 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: $hideButtonColor;
        font-weight: bold;
        font-size: 14px;
        cursor: pointer;
      }

    }
  }

  .tag-bottom-bar {
    position: relative;
    height: 100px;
    display: flex;

    .tag-bottom-bar-left {
      border-right: 1px solid #DBDBDB;
      position: relative;
      width: 60px;
      height: 100%;
    }

    .tag-bottom-bar-right {
      position: relative;
      -webkit-flex-grow: 1;
      flex-grow: 1;
      width: 70%;
      height: 100%;
      display: flex;
      flex-flow: column;
      padding: 10px;

      .tag-score-details-row {
        // border: 1px solid blue;
        width: 100%;
        font-size: 14px;
        margin-bottom: 4px;
        display: flex;
        justify-content: space-between;
        color: $interfaceGray;

        .tag-score-details-row-label {
          margin-left: 5px;
        }

        .tag-score-details-row-value {
          margin-right: 5px;
        }


      }

      .score-selected {
        font-weight: bold;
        color: black;
      }
    }

    .tag-key {
      position: absolute;
      height: 70%;
      width: 70%;
      top: 10px;
      left: 8px;
    }

  }

}

.tag-clear-icon {
  align-self: center;
  height: 0.6em !important;
  top: 0.1em;
  position: relative;

}

////////////////////// BLANK TAG CSS

.blank-tag-title-container {
  padding-left: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 15px;
  font-weight: bold;
}

.blank-tag-description-container {
  color: gray;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 0.8rem;
  text-align: center;
}