@import '_typography';
@import '_layout';
@import '_colors';


.radar-explain-title {
  margin-right: 10px;
  margin-left: 10px;
}

.radar-explain-type {
  text-transform: uppercase;
  font-size: 12px;
  color: gray;
  font-weight: bold;
  letter-spacing: 1px;
}

.indicator {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: gray;
}

.inline-title {
  display: inline-block;
}

.government {
  background-color: $government-solid;
}

.democracy {
  background-color: $democracy-solid;
}

.life {
  background-color: $life-solid;
}

.overall {
  background-color: black;
}

.radar-explain-unit {
  text-align: center;
}

.radar-explain-unit-container {
  display: flex;
  justify-content: space-between;
  margin: 40px;
}