@import "_colors";

.scatter-plot-container {
  height: 100%;
  width: 60%;
}

#svg-scatter-plot {
  // height: 600px;
  width: 100%;
}

.label>rect {
  display: none;
}

.label>circle {
  display: none;
}

.label>text {
  font-size: 12px;
  font-family: "Helvetica Neue", "Arial", sans-serif;
}

.scatter-xaxis>g>text {
  font-size: 9pt;
  color: $interfaceGray;
  font-family: "Helvetica Neue", "Arial", sans-serif;
}

.scatter-yaxis>g>text {
  font-size: 9pt;
  color: $interfaceGray;
  font-family: "Helvetica Neue", "Arial", sans-serif;
}