@import '_colors';
$panel-padding: 30px;

.UI-panel {
    padding: $panel-padding;
    border-radius: 4px;
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.1);
    background-color: white;
}

button {
    background-color: $interfacePurpleColor;
    padding: 15px 30px 15px 30px;
    border: none;
    color: white;
    font-size: 16px;
    border-radius: 4px;
    font-weight: bold;
    text-transform: capitalize;
    margin: auto;
}

button:hover {
    background-color: #4F208B;
}