@import '_colors';

.radar-chart-svg {
  width: 100%;
  height: 100%;
}

.radar-points {}

///////// TOOLTIP STYLING
.radar-tooltip {
  line-height: 1;
  font-weight: bold;
  font-size: 0.8rem;
  padding: 6px;
  background: white;
  border: 2px solid $interfacePurpleColor;
  color: black;
  border-radius: 6px;
  pointer-events: none;
}

/* Creates a small triangle extender for the tooltip */
.radar-tooltip:after {
  box-sizing: border-box;
  display: inline;
  font-size: 10px;
  width: 100%;
  line-height: 1;
  color: rgba(0, 0, 0, 0.8);
  position: absolute;
  pointer-events: none;
}

.tooltip-gray-part {
  color: $interfaceGray;
}

/* Northward tooltips */
.radar-tooltip.n:after {
  content: "\25BC";
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
  text-align: center;
  color: $interfacePurpleColor;
}

/* Eastward tooltips */
.radar-tooltip.e:after {
  content: "\25C0";
  margin: -4px 0 0 0;
  top: 50%;
  left: -8px;
}

/* Southward tooltips */
.radar-tooltip.s:after {
  content: "\25B2";
  margin: 0 0 1px 0;
  top: -8px;
  left: 0;
  text-align: center;
}

/* Westward tooltips */
.radar-tooltip.w:after {
  content: "\25B6";
  margin: -4px 0 0 -1px;
  top: 50%;
  left: 100%;
}

