@import '_colors';
@import '_constants';

.nav-bar-container {
  // border: 1px solid orange;  
  // height: 90px !important;
  display: flex;
  justify-content: space-between;
  border-top: 4px solid $titleBlue;
  // align-items: center;



  .branded-area-container {
    display: flex;
    margin-left: 30px;
    margin-top: 15px;

  }

  .brand-logo-box {
    padding-right: 30px;
    border-right: 1px solid lightgray;
    // float: left;
    margin-right: 30px;
    cursor: pointer;

  }


  .brand-app-title {
    font-size: 18px;
    font-weight: bold;
    color: $interfaceGray;
    margin-top: 10px;
    margin-bottom:0px;
  }

  .nav-links-container {
    margin-top: 14px;
    margin-right: 10px;
    margin-left: 60px;
    // border: 1px solid green;
    // float: left;



    .nav-link {
      // border: 1px solid blue;
      display: inline-block;
      margin-right: 30px;
      color: gray;
      cursor: pointer;
      text-transform: uppercase;
      font-size: 14px;
    }

  }

  .right-branded-area-container {
    // border: 1px solid red;
    display: inline-block;
    margin-right: 30px;
    font-size: 60px;
    cursor: pointer;
  }

}

.nav-link-li {
  list-style-type: none;
  // margin: 0px 10px;

}

#nav {
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 0;

  li {

    // font-weight: 600;
    letter-spacing: 0.3px;
    text-decoration: none;
    display: inline-block;
  }

  .nav-slider {
    height: 0.2rem;
    margin-top: 3px;
    background: $titleBlue;
    border: none;
    transition: .3s ease-in-out;
    display: block;
  }
}



@media screen and (min-width: 768px) {
  #nav hr {
    height: 0.2rem;
    margin-top: 3px;
    background: $titleBlue;
    border: none;
    transition: .3s ease-in-out;
    display: block;
  }
}

/* All mozilla versions */
@-moz-document url-prefix() {
  #nav hr {
    float: left;
  }
}

.nav-is-selected {
  font-weight: bold;
}


@media only screen and (max-width: $mobileBreakpoint) {

  .nav-bar-container {
    height: fit-content !important;
  }

  .right-branded-area-container {
    display: none !important;
  }

  .brand-logo-box {
    border: none !important;
  }

  .branded-area-container {

    flex-flow: column !important;

    .nav-links-container {
      margin: 0px 0px !important;

      #nav {
        margin-bottom: 0px !important;
      }
    }

    .brand-text-box {
      display: none !important;
    }
  }

}